import React, { useState, useEffect } from "react";
import { Link } from "gatsby";
import Layout from "../components/layout";
import Image from "../components/image";
import SEO from "../components/seo";

const QuizPage = () => {
  const [questions, setQuestions] = useState(null);
  //55840;
  useEffect(() => {
    fetch("/.netlify/functions/questions")
      .then((response) => response.json())
      .then((resultData) => {
        console.log(resultData);
        setQuestions(resultData);
      });
  }, []);

  const shuffleAnswers = (correct, incorrect) => {
    let final = [];

    final.push(...incorrect);
    final.push(correct);

    return final.shuffle();
  };

  const shuffle = (a) => {
    for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
  };

  //[{"category":"Entertainment: Books","type":"multiple","difficulty":"medium","question":"Who wrote the &quot;
  //A Song of Ice And Fire&quot; fantasy novel series?",
  //"correct_answer":"George R. R. Martin","incorrect_answers":["George Lucas","George Orwell","George Eliot"]}]

  return (
    <Layout>
      <SEO title="Questions" />
      <h1>Test Time</h1>
      {questions
        ? questions.map((q) => {
            return (
              <div>
                <h3>{q.category}</h3>
                <p>{q.question}</p>
                <ul></ul>
              </div>
            );
          })
        : null}
    </Layout>
  );
};

export default QuizPage;
